.App {
  position: relative;
  text-align: center;
  background-color: #FFC66F;
  margin: 0 0vw;
  /*padding: 0 10vw;*/
  height: 100vh;
  border-left: 8px ridge #ad8953;
  border-right: 8px groove #ad8953;
  overflow: hidden;
}

.App-logo {
  position: absolute;
  top: 25%;
  height: 40vmin;
  z-index: 10;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Setup {
  position: absolute;
  top: 60%;
  color: #493831;
  z-index: 10;
}

.line-a {
  position: absolute;
  bottom: 85%;
  width: 100%;
  height: 0;
  border-top: 6px double #c7b18f;
}

.line-b {
  position: absolute;
  bottom: 10%;
  width: 100%;
  height: 0;
  border-top: 6px double #c7b18f;
}

.Vanish {
  opacity: 1;
}

.op0 {
  opacity: 0;
}

.addEvents {
    pointer-events: all;
}

.noEvents {
  pointer-events: none;
}

.BG {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  min-width: 100%;
  min-height: 100%;
  object-fit: cover;
  opacity: 0;
  z-index: 1;
}

.BTN_font {
  position: relative;
  font-size: calc( 12px + 2vw );
  color: #52462f;
  z-index: 11;
}

.BTN_a {
  right: 12%;
  bottom: 52%;
  opacity: 0;
}

.BTN_b {
  right: 12%;
  bottom: 32%;
  opacity: 0;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .Setup {
    animation: Text-blinking infinite 3s cubic-bezier(.46,.84,.5,.95);
  }
}

@media (prefers-reduced-motion: no-preference) {
  .Launch {
    animation: Logo-Animation forwards 2s ease-out
  }
}

@media (prefers-reduced-motion: no-preference) {
  .Vanish {
    animation: vanishAnim forwards 1s ease-out
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes Text-blinking {
  0%, 100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}

@keyframes Logo-Animation {
  0% {
    transform: rotate(0deg);
    transform: translateY( 0% );
    opacity: 1;
  }
  10% {
    transform: rotate(90deg);
    opacity: 0;
  }
  100% {
    transform: translateY( -20% );
    opacity: 0;
  }
}

@keyframes vanishAnim {
  0% {
    opacity: 1;
  }
  50% {
    transform: translateY( 0% );
    opacity: 0;
  }
  100% {
    transform: translateY( -20% );
    opacity: 0;
  }
}