.regionBox {
    background: rgba(243, 236, 229, 0.664);
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    width: 88%;
    height: 0px;
    bottom: 0%;
    border-top: 8px groove #dac5a5;
    border-left: 8px groove #dac5a5;
    border-right: 8px ridge #dac5a5;
    border-top-left-radius: 4vmin;
    border-top-right-radius: 4vmin;
    opacity: 0;
    z-index: 5;
    overflow: hidden;
    white-space: nowrap;
}
.setMenu {
    animation: menuLoad forwards 0.7s cubic-bezier(.45,-0.45,.46,.92);
}

.menuText {
    margin-left: 3%;
    position: absolute;
    bottom: 76%;
    font-size: calc( 2pt + 3vh );
    color: #7c6d51;
}

.menuText::before {
    content: '～ メニュー ～';
}

.regionName {
    position: relative;
    top: 0%;
    font-size: calc( 4px + 8vmin );
    color: #4b453b;
}

.regionText {
    position: relative;
    top: -10%;
    font-size: calc( 2px + 4vmin );
    color: #5a5448;
}

.imgRegion {
    height: 6vi;
    border: 8px ridge #9e9383;
    border-radius: 3vh;
}

@keyframes menuLoad {
    0% {
        height: 0%;
        opacity: 0;
    }
    30% {
        height: 0%;
        opacity: 0;
    }
    100% {
        height: 30%;
        opacity: 1;
    }
}

.regTable {
    background: rgba(247, 243, 239, 0.747);
    display: flex;
    justify-content: center;
    flex-direction: column;
    position: absolute;
    width: 60%;
    height: 40%;
    bottom: 30%;
    border-top: 8px groove #dac5a5;
    border-left: 8px groove #dac5a5;
    border-bottom: 8px ridge #dac5a5;
    border-right: 8px ridge #dac5a5;
    border-radius: 4vmin;
    opacity: 0;
    z-index: 5;
}

.setTable {
    animation: tableLoad forwards 0.3s ease-out;
}

@keyframes tableLoad {
    from {
        bottom: 30%;
        opacity: 0;
    }
    to {
        bottom: 35%;
        opacity: 1;
    }
}

.leftTable {
    animation: tableLeft forwards 0.3s ease-out;
}

@keyframes tableLeft {
    from {
        bottom: 35%;
        opacity: 1;
    }
    to {
        bottom: 30%;
        opacity: 0;
    }
}

.closeApp {
    opacity: 1;
    animation: windowChange forwards 0.7s cubic-bezier(.66,1.03,.52,1.01);
}

@keyframes windowChange {
    0% {
        height: 0%;
    }
    100% {
        height: 99%;
    }
}