.App_animationed {
    margin: 0 10vw;
}

.curtain_noAnim {
    position: absolute;
    background: rgb(117, 84, 51);
    border-bottom: 8px dotted #443926;
    width: 100%;
    height:0%;
    top: 0%;
    opacity: 0;
    z-index: 10;
}

.curtain {
    background: rgb(117, 84, 51);
    border-bottom: 8px dotted #443926;
    width: 100%;
    height: 99%;
    top: 0%;
    animation: quizLoad forwards 0.7s cubic-bezier(.51,.01,.67,.3);
}

.quizBoard {
    position: absolute;
    align-items: center;
    justify-content: center;
    background: rgba(247, 233, 219, 0.452);
    border: 4px double #92784b;
    border-radius: calc( 4px + 4vmin );
    width: 50%;
    height: 40%;
    top: 8%;
    left: 25%;
    z-index: 11;
    opacity: 0;
    overflow: auto;
    scrollbar-width: none;
}

.quizBoard::-webkit-scrollbar {
    display: none;
}

.ansTable {
    position: absolute;
    align-items: center;
    width: 80%;
    height: 40%;
    top: 50%;
    left: 10%;
    z-index: 11;
    opacity: 0;
    pointer-events: all;
}

.ansBox1 {
    position: absolute;
    top: 10%;
    right: 54%;
    align-items: center;
    background: rgba(247, 233, 219, 0.452);
    border: 4px double #92784b;
    border-radius: calc( 4px + 4vmin );
    width: 30%;
    height: 20%;
    z-index: 18;
    overflow: hidden;
    white-space: nowrap;
}

.ansBox2 {
    position: absolute;
    top: 10%;
    left: 54%;
    align-items: center;
    background: rgba(247, 233, 219, 0.452);
    border: 4px double #92784b;
    border-radius: calc( 4px + 4vmin );
    width: 30%;
    height: 20%;
    z-index: 18;
    overflow: hidden;
    white-space: nowrap;
}

.ansBox3 {
    position: absolute;
    top: 44%;
    right: 54%;
    align-items: center;
    background: rgba(247, 233, 219, 0.452);
    border: 4px double #92784b;
    border-radius: calc( 4px + 4vmin );
    width: 30%;
    height: 20%;
    z-index: 18;
    overflow: hidden;
    white-space: nowrap;
}

.ansBox4 {
    position: absolute;
    top: 44%;
    left: 54%;
    align-items: center;
    background: rgba(247, 233, 219, 0.452);
    border: 4px double #92784b;
    border-radius: calc( 4px + 4vmin );
    width: 30%;
    height: 20%;
    z-index: 18;
    overflow: hidden;
    white-space: nowrap;
}

.setBoard {
    animation: boardLoad forwards 4.2s ease-out
}

@keyframes boardLoad {
    0% {
        top: 0%;
        opacity: 0;
    }
    66% {
        top: 0%;
        opacity: 0;
    }
    100% {
        top: 8%;
        opacity: 1;
    }
}

.ansBoard {
    animation: ansBoardLoad forwards 4.2s ease-out
}

@keyframes ansBoardLoad {
    0% {
        top: 46%;
        opacity: 0;
    }
    66% {
        top: 46%;
        opacity: 0;
    }
    100% {
        top: 54%;
        opacity: 1;
    }
}

.quizText {
    margin-left: 5%;
    margin-right: 5%;
    position: relative;
    font-size: calc( 8px + 3vmin );
    color: #865515;
    z-index: 18;
}

.ansText {
    position: absolute;
    top: -20%;
    right: 50%;
    transform: translateX( 50% );
    font-size: calc( 3px + 3vmin );
    color: #a07032;
}

.numText {
    position: absolute;
    top: 26%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: calc( 12px + 4vw );
    color: #ece1d3;
    z-index: 18;
}

@keyframes quizLoad {
    from {
        height: 99%;
    }
    to {
        height: 0%;
    }
}

.Sign {
    position: relative;
    align-items: center;
    justify-content: center;
    width: 50%;
    height: auto;
    top: 36%;
    left: 50%;
    z-index: 13;
    opacity: 0;
}

.imgSign {
    width: 80%;
    z-index: 15;
}

.setSign {
    animation: signLoad forwards 4.2s ease-out
}

@keyframes signLoad {
    0% {
        top: 100%;
        opacity: 0;
    }
    33% {
        top: 36%;
        opacity: 1;
    }
    66% {
        top: 36%;
        opacity: 1;
    }
    100% {
        top: 100%;
        opacity: 0;
    }
}

.closeCurtain_L {
    position: absolute;
    top: 0%;
    background: rgb(117, 84, 51);
    width: 0%;
    height: 100%;
    left: 0%;
    z-index: 20;
}

.closeCurtain_R {
    position: absolute;
    top: 0%;
    background: rgb(117, 84, 51);
    width: 0%;
    height: 100%;
    right: 0%;
    z-index: 20;
    opacity: 0;
}

.setClosing_L {
    opacity: 1;
    animation: curtClose forwards 1.4s ease-out;
}

.setClosing_R {
    opacity: 1;
    animation: curtClose forwards 1.4s ease-out;
}

@keyframes curtClose {
    0% {
        width: 0%;
    }
    50% {
        width: 50%;
    }
    100% {
        width: 50%;
    }
}

.resultOpen_L {
    animation: resultLoad forwards 0.6s ease-in;
    position: absolute;
    top: 0%;
    background: rgb(117, 84, 51);
    width: 50%;
    height: 100%;
    left: 0%;
    z-index: 20;
}

.resultOpen_Lf {
    position: absolute;
    top: 0%;
    background: rgb(117, 84, 51);
    width: 50%;
    height: 100%;
    left: 0%;
    z-index: 20;
}

.resultOpen_R {
    animation: resultLoad forwards 0.6s ease-in;
    position: absolute;
    top: 0%;
    background: rgb(117, 84, 51);
    width: 50%;
    height: 100%;
    right: 0%;
    z-index: 20;
    opacity: 1;
}

.resultOpen_Rf {
    position: absolute;
    top: 0%;
    background: rgb(117, 84, 51);
    width: 50%;
    height: 100%;
    right: 0%;
    z-index: 20;
    opacity: 1;
}

.resultClose {
    animation: resultLoad forwards 1s ease-in;
}

@keyframes resultLoad {
    from {
        width: 50%;
    }
    to {
        width: 0%;
    }
}

.resultTable {
    position: absolute;
    align-items: center;
    background: rgb(240, 227, 213);
    border: 6px ridge #7c6e56;
    border-radius: calc( 6px + 7vmin );
    width: 80%;
    height: 80%;
    top: 10%;
    left: 10%;
    z-index: 11;
    overflow: hidden;
}

.Result {
    margin: 10%;
    font-size: calc( 12pt + 16vmin );
    z-index: 20;
}

.T::before {
    content: "正解";
    color: #f53b3b;
}
.F::before {
    content: "不正解";
    color: #4f51e0;
}

.resultBTN_gu::before {
    font-size: calc( 4pt + 4vmin );
    content: "諦める";
}

.resultBTN_gu {
    position: absolute;
    bottom: 20%;
    right: 55%;
    background: #FFF;
    width: 30%;
    border: 4px ridge #836d5c;
    border-radius: calc( 3px + 4vmin );
    overflow: hidden;
    white-space: nowrap;
}

.resultBTN_rs::before {
    font-size: calc( 4pt + 4vmin );
    content: "次の問題へ";
}

.resultBTN_rs {
    bottom: 20%;
    left: 55%;
    position: absolute;
    background: #FFF;
    width: 30%;
    border: 4px ridge #836d5c;
    border-radius: calc( 3px + 4vmin );
    overflow: hidden;
    white-space: nowrap;
}

.resultText1 {
    font-size: calc( 4px + 4vmin );
}

.resultText2 {
    font-size: calc( 4px + 6vmin );
}

.resultText_eva {
    font-size: calc( 4px + 8vmin );
}

.toTitle {
    font-size: calc( 4px + 5vmin );
    position: absolute;
    left: 30%;
    bottom: 20%;
    width: 40%;
    height: auto;
    background: #f5e5b9;
    border: 4px ridge #836d5c;
    border-radius: calc( 3px + 6vmin );
    overflow: hidden;
    white-space: nowrap;
}

.toTitle::before {
    top: 50%;
    content: 'タイトルにもどる';
}

.Good {
    color: #f53b3b;
}
.Nice {
    color: #4f51e0;
}