.loadAnim_7 {
    pointer-events: none;
    animation: addMargin forwards 0.5s ease-out;
}

@keyframes addMargin {
    from {
        margin: 0 0vw;
    }
    to {
        margin: 0 10vw;
    }
}

.titleBar {
    background: rgb(117, 84, 51);
    display: flex;
    align-items: center;
    position: absolute;
    width: 100%;
    height: 0px;
    top: 0%;
    border-bottom-left-radius: 3vmin;
    border-bottom-right-radius: 3vmin;
    z-index: 10;
}

.titleOpening {
    pointer-events: none;
    animation: titleAnim forwards 1.2s ease-out;
}

@keyframes titleAnim {
    20% {
        height: 0px;
    }
    90% {
        height: calc( 50px + 5vh );
    }
    100% {
        height: calc( 50px + 5vh );
    }
}

#titleMain {
    margin-left: 3vh;
    opacity: 0;
    font-size: calc(26px + 3vmin);
    z-index: 10;
}

.setTitle {
    animation: titleLoad forwards 1.5s ease-out;
}

@keyframes titleLoad {
    0% {
        opacity: 0;
    }
    33% {
        opacity: 0;
        margin-left: 13vh;
    }
    100% {
        opacity: 1;
        margin-left: 3vh;
    }
}

.Title-logo {
    margin-left: 2vh;
    height: 88%;
    opacity: 0;
    z-index: 10;
}

.setTitleLogo {
    animation: titleLogoLoad forwards 1s cubic-bezier(.77,1.67,.78,.84);
}

@keyframes titleLogoLoad {
    0% {
        opacity: 0;
        rotate: 0deg;
    }
    100% {
        opacity: 1;
        rotate: 360deg;
    }
}

.setBG {
    animation: bgLoad forwards 1.5s ease-in;
}

@keyframes bgLoad {
    0% {
        opacity: 0;
    }
    33% {
        opacity: 0;
        transform: translate( -50% 30% );
    }
    100% {
        opacity: 1;
        transform: translate( -50% 0% );
    }
}

.homeButton {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 6px ridge #524631;
    border-radius: calc( 4px + 4vw );
    background-color: #fff9dd;
    height: calc( 16px + 4vh + 2vw );
    width: calc( 48px + 16vw );
    opacity: 0;
    z-index: 11;
}

.BTN_ab_grayout {
    background-color: #c0baa0;
}

.setButton_start {
    pointer-events: all;
    animation: BTN_a_load forwards 1.8s cubic-bezier(.53,.98,.73,1.03);
}

.setButton_exit {
    pointer-events: all;
    animation: BTN_b_load forwards 1.8s cubic-bezier(.53,.98,.73,1.03);
}

@keyframes BTN_a_load {
    0% {
        opacity: 0;
    }
    20% {
        opacity: 0;
        bottom: 52%;
    }
    80% {
        opacity: 1;
        bottom: 36%;
    }
    100% {
        opacity: 1;
        bottom: 36%;
    }
}

.leftBTN_a {
    animation: BTN_a_left forwards 0.4s cubic-bezier(.45,-0.45,.46,.92)
}

@keyframes BTN_a_left {
    from {
        opacity: 1;
        bottom: 36%;
        right: 12%;
    }
    to {
        opacity: 0;
        bottom: 140%;
        right: 12%;
    }
}

@keyframes BTN_b_load {
    0% {
        opacity: 0;
    }
    40% {
        opacity: 0;
        bottom: 32%;
    }
    100% {
        opacity: 1;
        bottom: 16%;
    }
}

.leftBTN_b {
    animation: BTN_b_left forwards 0.4s cubic-bezier(.45,-0.45,.46,.92)
}

@keyframes BTN_b_left {
    from {
        opacity: 1;
        bottom: 16%;
        right: 12%;
    }
    to {
        opacity: 0;
        bottom: 120%;
        right: 12%;
    }
}